import React,{Component} from 'react';
import {Link,IndexLink} from 'react-router';
import Header from '../components/header';

class Interiverlist extends Component{
	constructor(){
		super();
		this.state = {
			history:[]
		}
	}
	render (){
		var html;
		if(this.props.location.pathname.slice(-1) == 1){
			html = (
				<div className='inter-back'>
					<p className='empty-info'>
						<Link to='/' className='send'></Link>
						
					</p>
				</div>
			)
		} else {
			html = (
				<div className='inter-back'>
					<p className='empty-info'>
						<Link to='/' className='send'></Link>
						
					</p>
				</div>
			)
		}
		return(
			<div>
				<Header title="" comp='back'/>
				<div className="container">
					<div  className='interview'>
						<IndexLink to="/interviewlist" activeClassName='inter-active'></IndexLink>
						<Link to="/interviewlist/1" activeClassName='inter-active'></Link>
					</div>
					{html}
				</div>
			</div>
		)
	}
	componentWillMount(){
		// post
	}
}

export default Interiverlist


